import React  from 'react';
import { Form as RSForm } from 'reactstrap';
import IBase from '../Base.js'

class IForm extends IBase {

  render() {
    let {
      ...attributes
    } = this.props;

    const form = (
      <RSForm
        {...attributes}
      >
        {attributes.children}
      </RSForm>
    );

    return super.render(form);
  }
}

IForm.displayName = 'IForm';

export default IForm;